<template>
    <div class="g-content">
        <!--  -->
        <div class="m-query-form">
            <Form ref="queryForm" :model="queryForm" :rules="queryValid">
                <FormItem class="query-item" prop="id">
                    <Input v-model="queryForm.id" placeholder=""></Input>
                </FormItem>
                <FormItem class="query-item" prop="companyName">
                    <Input v-model="queryForm.companyName" placeholder=""></Input>
                </FormItem>
                <FormItem class="query-item" prop="linkMan">
                    <Input v-model="queryForm.linkMan" placeholder=""></Input>
                </FormItem>
                <FormItem class="query-item" prop="linkManPhone">
                    <Input v-model="queryForm.linkManPhone" placeholder=""></Input>
                </FormItem>
                <FormItem class="fl" prop="date">
                    <Date-picker class="query-item" type="date" v-model="queryForm.sTime" placement="bottom-end" placeholder="" @on-change="getStartDate"></Date-picker>
                    <Date-picker class="query-item" type="date" v-model="queryForm.eTime" placement="bottom-end" placeholder="" @on-change="getEndDate"></Date-picker>
                    <div class="clearfix"></div>
                </FormItem>
                <FormItem class="query-item" prop="emdnaledState">
                    <Select v-model="queryForm.dealState" placeholder="">
                        <Option value=""></Option>
                        <Option value="1"></Option>
                        <Option value="-1"></Option>
                    </Select>
                </FormItem>
                <FormItem class="fl">
                    <Button class="query-button" type="primary" @click="query('queryForm', 'valid')"></Button>
                    <Button class="query-button" type="ghost" @click="resetQuery('queryForm')"></Button>
                </FormItem>
            </Form>
            <div class="clearfix"></div>
        </div>
        <!--  -->
        <div class="m-operation">
            <Button class="operation-btn" :disabled="selectList.length == 0" type="primary" @click="deleteData"></Button>
            <div class="clearfix"></div>
        </div>
        <!--   -->
        <Loading v-if="pageLoading"></Loading>
        <div v-else>            
            <!--  -->
            <Table
                :row-class-name="rowClassName"
                class="m-table-list"
                border
                :columns="userList"
                :data="listData"
                @on-selection-change="setSelectList"
            ></Table>
            <!--  -->
            <Page
                class-name="m-page"
                show-elevator
                show-sizer
                show-total
                :total="page.dataCount"
                :page-size="page.pageSize"
                :current="page.pageNo"
                :page-size-opts="page.pageSizeOpts"
                @on-change="changePage"
                @on-page-size-change="changePageSize"
            >
            </Page>
            <div class="clearfix"></div>
        </div>
        <!-- -->
        <Modal v-model="showModal" width="500" @on-cancel="closeModal">
            <p slot="header">
                
            </p>
            <div>
                <table cellspacing="0" cellpadding="0" width="100%" class="m-table-info">
                    <tr>
                        <th></th>
                        <td>{{ paramsForm.companyName }}</td>
                    </tr>
                    <tr>
                        <th></th>
                        <td>{{ paramsForm.linkMan }}</td>
                    </tr>
                    <tr>
                        <th></th>
                        <td>{{ paramsForm.linkManPhone }}</td>
                    </tr>
                    <tr>
                        <th></th>
                        <td>{{ paramsForm.companyInfo }}</td>
                    </tr>
                    <tr>
                        <th></th>
                        <td>
                            <span v-if="paramsForm.dealState == 1" class="status-success"></span>
                            <span v-else class="status-wait"></span>
                        </td>
                    </tr>
                </table>
            </div>
            <div slot="footer">
                <Button size="large" @click="closeModal"></Button>
                <Button v-if="paramsForm.dealState == 1" type="primary" size="large" @click="closeModal"></Button>
                <Button v-else type="primary" size="large" @click="operation(paramsForm.id)"></Button>
            </div>
        </Modal>
    </div>
</template>

<script>
    // 
    import Loading from '@/components/Common/Loading'
    // JS
    import Common from 'common/common.js'
    // JS
    import Validate from 'common/validate.js'
    // Api
    // import Api from '@/api/api.js'
    // 
    import tableSetting from 'common/table_setting.js'
    // 
    import TableQuery from 'mixins/table_query.js'
    // 
    import TableOperate from 'mixins/table_operate.js'
    // 
    import Page from 'mixins/page.js'

    export default {
        components: { Loading },
        mixins: [  TableQuery, TableOperate, Page ],
        data() {
            return {
                // 
                pageLoading: false,
                // 
                caseTypeList: [],
                // 
                queryForm: {
                    // 
                    id: '',
                    // 
                    companyName: '',
                    // 
                    linkMan: '',
                    // 
                    linkManPhone: '',
                    // 
                    sTime: '',
                    // 
                    eTime: '',
                    // 
                    dealState: '',
                },
                // 
                queryValid: {},
                // 
                paramsForm: {
                    id: '',                                        
                    companyName: '',
                    linkMan: '',
                    linkManPhone: '',
                    companyInfo: '',
                    dealState: ''
                },
                // 
                paramsValid: {
                    linkManPhone:[{ pattern: Common.regMobile , message: '', trigger: 'blur' }],
                },
                // 
                showModal: false,
                // :1, 2
                operateType: 0,
                // 
                userList:[
                    {
                        title: '',
                        type: 'selection',
                        key: 'selectAll',
                        width: '60',
                        align: 'center',
                    },
                    {
                        title: '',
                        key: 'id',
                        align: 'center',
                    },
                    {
                        title: '',
                        key: 'companyName',
                        align: 'center'
                    },
                    {
                        title: '',
                        key: 'linkMan',
                        align: 'center'
                    },
                    {
                        title: '',
                        key: 'linkManPhone',
                        align: 'center'
                    },
                    {
                        title: '',
                        key: 'createTime',
                        align: 'center',
                    },
                    {
                        title: '',
                        key: 'dealState',
                        align: 'center',
                        render: (h, params) => {
                            return h('span', {
								attrs: {
									class: params.row.dealState == 1 ? 'status-success' : 'status-wait',
								}
							}, params.row.dealState == 1 ? '' : '');
                        }
                    },
                    {
                        title: '',
                        width: '120',
                        key: 'operation',
                        align: 'center',
                        render: (h, params) => {
                            return h('div', [
                                tableSetting.popUp(h, params, this, '', 0),
                            ]);                          
                        }
                    }
                ],
                //
                listData:[
                    {
                        id: 1,
                        companyName: '',
                        linkMan: '',
                        linkManPhone: '18376686974',
                        createTime: '2018-2-28',
                        dealState: 1
                    },
                    {
                        id: 2,
                        companyName: '',
                        linkMan: '',
                        linkManPhone: '18376686974',
                        createTime: '2018-2-28',
                        dealState: -1
                    },
                    {
                        id: 3,
                        companyName: '',
                        linkMan: '',
                        linkManPhone: '18376686974',
                        createTime: '2018-2-28',
                        dealState: -1
                    }
                ],
            }
        },
        created(){
            // 
            this.$store.commit('SET_BREADCRUMB', [
                { name: '', path: '/Home' },
                { name: '', path: '/Broadcast/List' }
            ]);
            // 
            this.getTableList();
        },
        methods: {
            // 
            getTableList(query){
                // this.pageLoading = true;
                // 
                if(query){
                    this.isQuery = true;
                }
                else{
                    this.isQuery = false;
                }
            },
            // 
            openModel(params){
                this.showModal = true;
                this.paramsForm.id = params.id;
                this.paramsForm.companyName = params.companyName;
                this.paramsForm.linkMan = params.linkMan;
                this.paramsForm.linkManPhone = params.linkManPhone;
                this.paramsForm.companyInfo = '666';
                this.paramsForm.dealState = params.dealState;               
            },
            // 
            closeModal(){
                this.showModal = false;
                this.paramsForm = {};
            },
            // 
            operation(id){
                this.closeModal();
            }
        }
    }
</script>

<style lang="less" scoped>
    @import "../../assets/less/table_list";
    .m-table-info {
        th,td {
            padding: 12px;
            vertical-align: top;
        }
        th {
            width: 110px;
            text-align: right;
        }
    }
</style>


