<template>
    <div :class="['g-layout', sidebarSpan < 3 ? 'layout-mini' : '']">
        <div class="layout-left">
        	<!--  -->
            <Sidebar></Sidebar>
        </div>
        <div class="layout-right">
        	<!--  -->
            <MenuTop></MenuTop>
            <LocatePath></LocatePath>
            <!--  -->
            <router-view></router-view>
	        <!--  -->
            <div class="copyright">
                &copy; 2018   ICP16008233-1
            </div>
        </div>
    </div>
</template>
<script>
	// 
	import MenuTop from 'components/Common/MenuTop'
  	import Sidebar from 'components/Common/Sidebar'
    import LocatePath from 'components/Common/LocatePath'
    // Vuex
    import { mapGetters } from 'vuex'

    export default {
    	components:{ MenuTop, Sidebar, LocatePath },
        computed: {
            ...mapGetters([ 'sidebarSpan', 'breadcrumb' ]),
        },
        data () {
            return {
            }
        },
    }
</script>
<style lang="less">
    .g-layout{
        position: relative;
        min-width: 1200px;
    }
    .layout-left{
        height: 100%;
        width: 200px;
        position:absolute;
        z-index: 10;
        left:0;
        background: #4d5256;
        transition: width .2s ease-in-out;
        transition: width .2s ease-in-out;
        -moz-transition: width .2s ease-in-out; /* Firefox 4 */
        -webkit-transition: width .2s ease-in-out; /* Safari and Chrome */
        -o-transition: width .2s ease-in-out; /* Opera */
    }
    .layout-right{
        overflow: hidden;
        min-height: 100vh;
        position: relative;
        width: 100%;
        padding-left: 200px;
        transition: padding-left .2s ease-in-out;
        -moz-transition: padding-left .2s ease-in-out; /* Firefox 4 */
        -webkit-transition: padding-left .2s ease-in-out; /* Safari and Chrome */
        -o-transition: padding-left .2s ease-in-out; /* Opera */
    }

    .g-layout.layout-mini{
        .layout-left{
            width: 68px;
        }
        .layout-right{
            padding-left: 68px;
        }
    }

    .g-breadcrumb{
        padding: 10px 15px 0;
    }

    .g-content{
        min-height: 84vh;
        margin: 15px;
        background: #fff;
        border-radius: 4px;
        padding: 15px;
        position: relative;
    }

    .content-main{
        padding: 10px;
    }

    .copyright{
        width: 100%;
        text-align: center;
        padding: 10px 15px;
        color: #9ea7b4;
        background: #eee;
    }
</style>



