import 'babel-polyfill'
import Vue from 'vue'
import App from './App'
import router from './router'
import store from './store'
// iView
import iView from 'iview'
// iView
// css-loader,style-loaderless-loader
// vue-cliloaderwebpackvue-cliloader
import './theme/index.less'
// cookie
import { GetCookie } from './common/important'
// 
import VueQuillEditor from 'vue-quill-editor'

Vue.use(iView)
Vue.use(VueQuillEditor)
Vue.config.productionTip = false

// 
const whiteList = [ '404', 'Login']

router.beforeEach((to, from, next) => {
	// token
	let isLogin = Boolean(GetCookie('pAppToken'));
	// 
	if(isLogin){
		// 
		if(to.name === 'Login') next({ name : 'Main'});
		else{
			// token
			store.dispatch('CheckToken').then(res => { next() }).catch(err => { next({ name : 'Login'}) })
		}
	}
	// token
	else{
		// 
		if(whiteList.indexOf(to.name) !== -1) next();
		else next({ name : 'Login'});
	}
})

new Vue({
	el: '#app',
	router,
	store,
	template: '<App/>',
	components: { App }
})



