<template>
  	<div id="login" class="m-login">
       <Form ref="loginForm" :model="loginForm" :rules="validate" class="login-form">
       		<h3 class="form-title"><img :src="logo"/></h3>
	        <Form-item prop="username">
	            <Input v-model="loginForm.username" placeholder="" @on-enter="submit('loginForm')">
	            	<Icon type="ios-person-outline" slot="prepend"></Icon>
	            </Input>
	        </Form-item>
	        <Form-item prop="password">
	            <Input type="password" v-model="loginForm.password" placeholder="" @on-enter="submit('loginForm')">
	            	<Icon type="ios-locked-outline" slot="prepend"></Icon>
	            </Input>
	        </Form-item>
	        <Form-item>
	            <Checkbox v-model="remeberPwd"></Checkbox>
	            <!-- <router-link to="/SendVerifyCode" class="fr"></router-link> -->
	        </Form-item>
	        <Form-item>
	            <Button type="primary" long @click="submit('loginForm')" :loading="loading"></Button>
	        </Form-item>
	        <!-- <Form-item>
	            <Button type="primary" long @click=""></Button>
	        </Form-item> -->
	        <!--<div style="text-align:center">:17777075292:mimashi123</div>-->
	    </Form>
	</div>
</template>

<script>
	import { SetCookie, SetLocalS, GetLocalS, Encrypt, Decrypt } from 'common/important.js'
  import Common from '@/common/common.js'
	 // Api
	 import Login from 'api/login.js'

	export default {
		data(){
			return {
				// 
				loading: false,
				// logo
				logo: require('assets/images/logo.png'),
				// 
				loginForm:{
					// 
					username: '',
					// 
					password: '',
				},
        // cdKey
        startTime: '',
        // cdKey
        endTime: '',
				// 
				remeberPwd: false,
				// 
				validate: {
          username: [{ required: true, message: '', trigger: 'blur' }],
          password: [{ required: true, message: '', trigger: 'blur' }],
        }
			}
		},
		created() {
			// 
			if (GetLocalS('username')) {
				// 
				this.loginForm.username = GetLocalS('username');
				this.loginForm.password = Decrypt(GetLocalS('password'));
				this.remeberPwd = true;
			}
		},
		mounted(){
			const loginBg = document.getElementById('login');
			// 
			loginBg.style.height = document.body.scrollHeight + 'px';
			// 
            window.onresize = () => {
				loginBg.style.height = document.body.scrollHeight + 'px';
            }
		},
		methods:{
			// 
			submit (form){
        this.$refs[form].validate((valid) => {
          if (valid) {
            this.loading = true
            Login.Login(this.loginForm).then(res=>{
              this.loading = false
//              console.log(res)
              if(res.code==200){
                // tokencookie
                SetCookie('pAppToken', res.data.token);
                this.$store.commit('SET_USER_TOKEN', res.data.token);
                this.$Message.success('!');
                // 
                if (this.remeberPwd) {
                  // 
                  SetLocalS('username', this.loginForm.username);
                  SetLocalS('password', Encrypt(this.loginForm.password));
                }
                this.$router.push({ name: 'Main' });
                return true;
              }
              else if(res.code==0){
                this.$Message.error(res.msg);
              }
            }).catch(err=>{
              // 
              this.$Message.error('!');
              this.loading = false;
            })
          }
          else this.$Message.error('!');
        })
      },
		}
	}
</script>
<style lang="less" scoped>
	.m-login {
	    position: relative;
	    width: 100%;
	    height: 100%;
	    height: 100vh;
		min-height: 900px;
	    background-color: #4d5256;
	}

	.login-form{
	    position: absolute;
	    left: 50%;
		top: 50%;
	    width: 400px;
	    padding: 35px 35px 15px;
	    margin: auto;
		margin-top: -250px;
		margin-left: -200px;
	    background: #fff;
	    border: 1px solid #eaeaea;
	    border-radius: 5px;
		-moz-border-radius: 5px;
		-webkit-border-radius: 5px;
		box-shadow: 0 0 25px #555;
	}

	.form-title{
		font-size: 24px;
		font-weight: 400;
		color: #555;
		margin: 0 auto 20px;
		text-align: center;
		font-weight: 700;

		img{
			display: inline-block;
		}
	}
</style>



