<template>
    <div class="g-content">
        <!--  -->
        <div class="m-query-form">
            <Form ref="queryForm" :model="queryForm" :rules="validate">
                <Form-item class="query-item" prop="id">
                    <Input v-model="queryForm.id" placeholder=""></Input>
                </Form-item>
                <Form-item class="query-item" prop="mobile">
                    <Input v-model="queryForm.mobile" placeholder=""></Input>
                </Form-item>
                <Form-item class="query-item" prop="email">
                    <AutoComplete v-model="queryForm.email" :data="emailList" @on-search="selectEmail" @on-select="setEmail" placeholder=""></AutoComplete>
                </Form-item>
                <Form-item class="fl" prop="date">
                    <Date-picker class="query-item" type="date" v-model="queryForm.sTime" placement="bottom-end" placeholder="" @on-change="getStartDate"></Date-picker>
                    <Date-picker class="query-item" type="date" v-model="queryForm.eTime" placement="bottom-end" placeholder="" @on-change="getEndDate"></Date-picker>
                    <div class="clearfix"></div>
                </Form-item>
                <Form-item class="query-item">
                    <Select v-model="queryForm.state" placeholder="">
                        <Option value=""></Option>
                        <Option value="1"></Option>
                        <Option value="0"></Option>
                    </Select>
                </Form-item>
                <Form-item class="fl">
                    <Button class="query-button" type="primary" @click="query('queryForm', 'valid')"></Button>
                    <Button class="query-button" type="ghost" @click="resetQuery('queryForm')"></Button>
                </Form-item>
            </Form>
            <div class="clearfix"></div>
        </div>
        <!--  -->
        <div class="m-operation">
            <router-link :to="{ name: 'StoreAccount' }" class="operation-btn ivu-btn ivu-btn-primary"></router-link>
            <Button class="operation-btn" :disabled="selectList.length == 0" type="primary" @click="deleteData"></Button>
            <Button class="operation-btn" :disabled="selectList.length == 0" type="primary" @click="enableOrDisable(1)"></Button>
            <Button class="operation-btn" :disabled="selectList.length == 0" type="primary" @click="enableOrDisable(-1)"></Button>
            <div class="clearfix"></div>
        </div>
        <!--   -->
        <Loading v-if="pageLoading"></Loading>
        <div v-else>
            <!--  -->
            <Table
                :row-class-name="rowClassName"
                class="m-table-list"
                border
                :columns="userList"
                :data="listData"
                @on-selection-change="setSelectList"
            ></Table>
            <!--  -->
            <Page
                class-name="m-page"
                show-elevator
                show-sizer
                show-total
                :total="page.totalSize"
                :page-size="page.size"
                :current="page.page"
                :page-size-opts="page.pageSizeOpts"
                @on-change="changePage"
                @on-page-size-change="changePageSize"
            >
            </Page>
            <div class="clearfix"></div>
        </div>
    </div>
</template>

<script>
  import { SetCookie, SetLocalS, GetLocalS, Encrypt, Decrypt } from 'common/important.js'
    // 
    import Loading from '@/components/Common/Loading'
    // JS
    import Common from 'common/common.js'
    // JS
    import Validate from 'common/validate.js'
    // Api
     import Account from '@/api/Account.js'
    // 
    import EmailComplete from 'mixins/email_complete.js'
    // 
    import tableSetting from 'common/table_setting.js'
    // 
    import TableQuery from 'mixins/table_query.js'
    // 
    import TableOperate from 'mixins/table_operate.js'
    // 
    import Page from 'mixins/page.js'

    export default {
        components: { Loading },
        mixins: [ EmailComplete, TableQuery, TableOperate, Page ],
        computed: {
          //
          apiDisable(){
            return()=>Account.Disable('User',this.selectList)
          },
          // 
          apiEnable(){
            return () => Account.Enabled('User',this.selectList);
          },
          // 
          apiDelete(){
            return () => Account.Delete('User',this.selectList);
          },
        },
        data() {
            return {
                // 
                pageLoading: false,
                // 
                jobList: [],
                // 
                provinceList: [],
                // 
                queryForm: {
                    // 
                    id: '',
                    // 
                    mobile: '',
                    // 
                    email: '',
                    // 
                    sTime: '',
                    // 
                    eTime: '',
                    // 
                    state: '',
                },
                // 
                validate: {
                    email:[{ pattern: Common.regEmail , message: '' }],
                    mobile:[{ pattern: Common.regMobile , message: '', trigger: 'blur' }],
                    date:[{
                        validator: (rule, value, callback) => {
                            Validate.ValidRangeDate(this.queryForm.sTime, this.queryForm.eTime, callback, false);
                        },
                        trigger: 'change',
                    }]
                },
                // 
                userList: [
                    {
                        title:'',
                        type: 'selection',
                        width: 60,
                        align: 'center'
                    },
                    {
                        title: '',
                        key: 'id',
                        align: 'center'
                    },
                    {
                        title: '',
                        key: 'userName',
                        align: 'center'
                    },
                    {
                        title: '',
                        key: 'mobile',
                        align: 'center'
                    },
                    {
                        title:'',
                        key:'email',
                        align:'center'
                    },
                    {
                        title: '',
                        key: 'createTime',
                        align: 'center',
                    },
                    {
                        title: '',
                        key: 'userType',
                        align: 'center',
                    },
                    {
                        title:'',
                        key:'enabledState',
                        align:'center',
                        render:(h,params)=>{
                            return h('span',{
                                attrs:{
                                    class: params.row.enabledState == 1 ? 'status-success' : 'status-fail',
                                }
                            },params.row.enabledState == 1 ? '' : '')
                        }
                    },
                    {
                        title: '',
                        key: 'operation',
                        align: 'center',
                        render: (h, params) => {
                            return h('div', [
                                tableSetting.gotoDetail(h, params, this, '', 'StoreAccount'),
                            ]);
                        }
                    }
                ],
                //
                listData:[
//                    {
//                        id: 1,
//                        realname: 'xLong',
//                        gender: '',
//                        mobile: '18376686974',
//                        email: 'by_xlong@yeah.net',
//                        createTime: '2018-2-28',
//                        enabledState: 1
//                    },
                ],
            }
        },
        created(){
            // 
            this.$store.commit('SET_BREADCRUMB', [
                { name: '', path: '/Home' },
                { name: '', path: '/AccountManage/List' }
            ]);
            // 
            this.getTableList();
        },
        methods: {
            // 
            getTableList(query){
                 this.pageLoading = true
//                console.log(this.page.page)
//                console.log(this.page.size)
//                console.log(this.queryForm)
                Account.List(this.queryForm,this.page.page,this.page.size).then(res=>{
//                    console.log(res)
                  this.pageLoading = false
                  if(res.code==200){
                    // 
                    if(query) this.isQuery = true
                    else this.isQuery = false
                    //
                    this.page = res.paging
                    this.listData = res.data
                  }else this.$Message.warning(res.msg)
                }).catch(err=>{
                  console.log(err)
                })
            },
            // 
            setListData(result){

            },
            handleSubmit (name) {
                this.$refs[name].validate((valid) => {
                    if (valid) {
                        this.$Message.success('Success!');
                    } else {
                        this.$Message.error('Fail!');
                    }
                })
            },
            handleReset (name) {
                this.$refs[name].resetFields();
            }
        }
    }
</script>

<style lang="less" scoped>
    @import "../../assets/less/table_list";
</style>



