<template>
    <div class="g-content">
        <!--   -->
        <Loading v-if="pageLoading"></Loading>
        <Form v-else ref="infoForm" :model="infoForm" :rules="validate" :label-width="120">
            <Row>
                <Col span="12">
                    <!-- <Form-item label="" prop="companyAddr">
                        <Input v-model="infoForm.companyAddr" placeholder=""></Input>
                    </Form-item> -->
                    <Form-item label="" prop="contactMan">
                        <Input v-model="infoForm.contactMan" placeholder=""></Input>
                    </Form-item>
                    <Form-item label="" prop="contactPhone">
                        <Input v-model="infoForm.contactPhone" placeholder=""></Input>
                    </Form-item>
                    <Form-item label="" prop="contactEmail">
                        <Input v-model="infoForm.contactPhone" placeholder=""></Input>
                    </Form-item>
                    <Form-item>
                        <Button class="fl" type="primary" @click="submit('infoForm', 'edit')"></Button>
                    </Form-item>              
                </Col>
            </Row>
        </Form>
    </div>
</template>

<script>
    export default {
        data(){
            return{
                // 
                pageLoading: false,
                // 
                infoForm: {
                    // 
                    // companyAddr: '',
                    // 
                    contactMan: '',            
                    // 
                    contactPhone: '',
                    // 
                    contactEmail: ''
                },
                // 
                validate: {
                    // companyAddr: [{ required: true, message: '', trigger: 'blur'}],
                    contactMan: [{ required: true, message: '', trigger: 'blur'}],
                    contactPhone: [{ required: true, message: '', trigger: 'blur'}],
                    contactEmail: [{ required: true, message: '', trigger: 'blur'}]
                }
            }
        },
        created(){
            // 
            this.$store.commit('SET_BREADCRUMB', [
                { name: '', path: '/Home' },
                { name: '', path: '/ContactManage/ContactWay' }
            ]);
        },
        methods:{
            // 
            submit(form, type) {
                this.$refs[form].validate((valid) => {
                    if (valid) {
                        // 
                        // this.pageLoading = true;
                    }
                    else this.$Message.error('');
                })    
            },
            // 
            getDetail(){
            }
        }
    }
</script>

<style lang="less" scoped> 
    .m-operation {
        button, a {
            min-width: 80px;
            margin-left: 10px;
        }
    }
</style>



