import Common from 'common/common.js'

export default {
  data() {
    return {
      // 
      page: {
        // 
        totalSize: 0,
        // 
        hasNextPage: false,
        // 
        hasPreviousPage: false,
        // 
        pageCount: 0,
        // 
        page: 1,
        // 
        size: 10,
        // 
        pageSizeOpts: [10, 20, 30, 40]
      },
    }
  },
  methods: {
    // 
    setPage(result){
      this.page.totalSize = result.count;
      this.page.pageCount = result.pages;
    },
    // 
    changePage(page) {
      // 
      this.page.page = page;
      // 
      this.updateList();
    },
    // 
    changePageSize(size){
      console.log(size)
      // 
      this.page.size = size;
      // 
      this.updateList();
    },
    // 
    updateList(){
      // 
      this.isQuery ? this.getTableList(true) : this.getTableList();
    },
  }
}



