import Common from './common.js'

export default {
    // 
    ValidPassword: (password, callback, required) => {
        // 
        if(password == ''){
            // 
            if(required) return callback(new Error(''));
            else return callback();
        }
        else{
            if(!Common.regPassword.test(password)) return callback(new Error('6-12'));
            else return callback();
        }
    },
    // 
    ValidPwdCheck: (password, pwdCheck, callback, required) => {
        // 
        if(pwdCheck == ''){
            // 
            if(required) return callback(new Error(''));
            else {
                if(password != '') return callback(new Error(''));
                else return callback();
            }
        }
        else{
            if(password != pwdCheck){
                return callback(new Error(''));
            }
            else return callback();
        }
    },
    // 
    ValidBirthDate: (birhtDate, callback, required,type) => {
        // 
        if(birhtDate == ''){
            // 
            if(required) return callback(new Error(''));
            else return callback();
        }
        else{
            let nowDate = new Date();
            const compare = Common.CompareDate(birhtDate, nowDate);
            if (!compare&&type!='date') {
              return callback(new Error(''));
            }
            else if(!compare&&type=='date') {
              return callback(new Error(''));
            }
            else return callback();
        }
    },
    // 
    ValidProvince: (province, callback, required) => {
        // 
        if(required){
            // 
            if(province == ''){
                return callback(new Error(''));
            }
            else return callback();
        }
        else return callback();
    },
    // 
    ValidAgreement: (agerrment, errorText, callback, required) => {
        if(required){
            if (!agerrment) {
                return callback(new Error(errorText));
            }
            else return callback();
        }
        else return callback();
    },
    // 
    ValidCode: (code, callback, required) => {
        if(required){
            if(!code){
                return callback(new Error(''));
            }
            else return callback();
        }
        else return callback();
    },
    // 
    ValidUserName: (userName, callback, required) => {
        if(required){
            if(!userName){
                return callback(new Error(''));
            }
            else if (!Common.regEmail.test(userName) && !Common.regEmail.Mobile.test(userName)) {
                return callback(new Error(''));
            }
            else return callback();
        }
        else return callback();
    },
    // 
    ValidRangeDate: (startDate, endDate, callback, required) => {
        // 
        if(required && startDate == '') return callback(new Error(''));
        if(required && endDate == '') return callback(new Error(''));
        else{
            if(startDate){
                // let nowDate = new Date();
                // const compare = Common.CompareDate(startDate, nowDate);
                //
                // if (!compare) return callback(new Error(''));
                // else{
                    if(endDate){
                        const compare = Common.CompareDate(startDate, endDate);
                        if (!compare) return callback(new Error(''));
                        else return callback();
                    }
                    else return callback();
                // }
            }
            else{
                if(endDate) return callback(new Error(''));
                else return callback();
            }
        }
    }
}



