<template>
    <div class="g-content">
        <Loading v-if="pageLoading"></Loading>
        <Form v-else ref="infoForm" :model="infoForm" :rules="validate" label-position="right" :label-width="150">
            <div class="m-content">
                <Row>
                    <Col span="12">
                        <Form-item label="" prop="title">
                            <Input v-model="infoForm.title" placeholder=""></Input>
                        </Form-item>
                        <Form-item label="">
                            <span>{{infoForm.webSite}}</span>
                        </Form-item>
                        <Form-item label="">
                            <span>{{infoForm.createTime}}</span>
                        </Form-item>
                    </Col>
                    <Col span="12">
                        <Form-item label="" prop="keyWord">
                            <Input v-model="infoForm.keyWord" placeholder="','"></Input>
                        </Form-item>
                        <Form-item label="">
                            <span class="text-break">{{infoForm.url}}</span>
                        </Form-item>
                        <Form-item prop="publishDate" class="fl" label="">
                            <Date-picker class="query-item" type="date" v-model="infoForm.publishDate" placement="bottom-end" placeholder="" @on-change="getDate"></Date-picker>
                            <div class="clearfix"></div>
                        </Form-item>
                    </Col>
                </Row>
                <Row>
                    <Col>
                        <Form-item label="" prop="content">
                            <tinymce v-model="infoForm.htmlContext" height="500" :toolbar="'code | preview fullscreen | insertfile undo redo | styleselect | fontsizeselect | bold italic | forecolor backcolor | alignleft aligncenter alignright alignjustify | bullist numlist outdent indent | link'"></tinymce>
                        </Form-item>
                    </Col>
                </Row>
            </div>
            <!--  -->
            <div class="m-operation">
                <Button class="fr" type="primary" @click="submit('infoForm', 'edit')"></Button>
                <Button class="fr" type="ghost" @click="$router.go(-1)"></Button>
                <div class="clearfix"></div>
            </div>
        </Form>
    </div>
</template>
<script>
    // 
    import Loading from 'components/Common/Loading'
    // JS
    import Common from 'common/common.js'
    // JS
    import Validate from 'common/validate.js'
    // 
    import TableQuery from 'mixins/table_query.js'
    // api
    import Article from 'api/article.js'
    //
    import Tinymce from "@/components/Common/Tinymce.vue"
    export default {
        components: { Loading , Tinymce },
        mixins: [ TableQuery ],
        data() {
            return {
                // 
                pageLoading: false,
                //
                infoForm:{
                    //id
                    id:'1',
                    //
                    title:'',
                    //
                    keyWord:'',
                    //
                    createTime:'2018-2-25',
                    //
                    publishDate:'2018-2-27',
                    //
                    url:'www.smt.com',
                    //
                    webSite:'',
                    //
                    htmlContext:''
                },
                validate:{},
            }
        },
        created(){
            this.$store.commit('SET_BREADCRUMB', [
                { name: '', path: '/Home' },
                { name: '', path: '/ArticleManage/List' },
                { name: '', path: '' }
            ]);
            this.getDetail()
        },
        methods:{
            // 
            submit(form, type) {
              if(typeof (this.infoForm.publishDate)=='object'){
                var d = this.infoForm.publishDate
                var month = (d.getMonth() + 1) > 10? (d.getMonth() + 1): '0'+(d.getMonth() + 1)
                var date = d.getDate() > 10? d.getDate: '0'+d.getDate()
                this.infoForm.publishDate=d.getFullYear() + '-' + month + '-' + date
              }
                this.$refs[form].validate((valid) => {
                    if (valid) {
                      // 
                       this.pageLoading = true;
                      Article.Edit(this.infoForm,this.$route.query.id).then(res=>{
                        this.pageLoading = false
                        if(res.code==200){
                          this.$Message.success({
                            content: '!',
                            onClose: () => {
                              // 
                              this.$router.push({ name: 'ArticleManage' })
                            }
                          })
                        }else this.$Message.warning(res.msg)
                      }).catch(err=>{
                        console.log(err)
                        this.$Message.error('')
                      })
                    }
                    else this.$Message.error('');
                })
            },
            getDetail(){
                this.pageLoading = true
//                console.log(this.$route.query.id)
                Article.GetInfo(this.$route.query.id).then(res=>{
                    this.pageLoading = false
//                    console.log(res)
                    if(res.code==200){
                        this.infoForm = res.data
                    }else this.$Message.error(res.msg)
                }).catch(err=>{
                  this.$Message.error('')
                })
            }
        }
    }
</script>
<style lang="less" scoped>
    .g-content{
        padding: 15px 60px 15px 0;
    }
    .m-operation {
        button, a {
          min-width: 80px;
          margin-left: 10px;
        }
    }
    .text-break{
      display: inline-block;
      word-wrap: break-word;
      white-space: normal;
      width: 100%;
    }
</style>



