export default {
	data() {
		return {
            // 
            emailList: [],
		}
    },
    created(){},
	methods: {
		// 
        selectEmail(value) {
            this.emailList = !value || value.indexOf('@') >= 0 ? [] : [
                value + '@qq.com',
                value + '@sina.com',
                value + '@dingtalk.com',
                value + '@115.com',
                value + '@163.com',
            ];
        },
        // 
        setEmail(value){
            if(this.infoForm) this.infoForm.email = value;
            else if(this.queryForm) this.queryForm.email = value; 
        }
	}
}


