<template>
    <div class="g-content">
        <!--  -->
        <div class="m-query-form">
            <Form ref="queryForm" :model="queryForm" :rules="validate">
                <Form-item class="query-item" prop="id">
                    <Input v-model="queryForm.id" placeholder=""></Input>
                </Form-item>
                <Form-item class="query-item" prop="title">
                    <Input v-model="queryForm.title" placeholder=""></Input>
                </Form-item>
                <Form-item class="query-item" prop="webSite">
                    <Input v-model="queryForm.webSite" placeholder=""></Input>
                </Form-item>
                <Form-item class="fl" prop="date">
                    <Date-picker class="query-item" type="date" v-model="queryForm.sTime" placement="bottom-end" placeholder="" @on-change="getStartDate"></Date-picker>
                    <Date-picker class="query-item" type="date" v-model="queryForm.eTime" placement="bottom-end" placeholder="" @on-change="getEndDate"></Date-picker>
                    <div class="clearfix"></div>
                </Form-item>
                <Form-item class="query-item" prop="enabled">
                    <Select v-model="queryForm.state" placeholder="">
                        <Option value=""></Option>
                        <Option value="1"></Option>
                        <Option value="0"></Option>
                    </Select>
                </Form-item>
                <Form-item class="fl">
                    <Button class="query-button" type="primary" @click="query('queryForm', 'valid')"></Button>
                    <Button class="query-button" type="ghost" @click="resetQuery('queryForm')"></Button>
                </Form-item>
            </Form>
            <div class="clearfix"></div>
        </div>
        <!--  -->
        <div class="m-operation">
          <Button class="operation-btn" :disabled="selectList.length == 0" type="primary" @click="deleteData"></Button>
          <Button class="operation-btn" :disabled="selectList.length == 0" type="primary" @click="enableOrDisable(1)"></Button>
          <Button class="operation-btn" :disabled="selectList.length == 0" type="primary" @click="enableOrDisable(-1)"></Button>
          <div class="clearfix"></div>
        </div>
        <!--   -->
        <Loading v-if="pageLoading"></Loading>
        <div v-else>
            <!--  -->
            <Table
                :row-class-name="rowClassName"
                class="m-table-list"
                border
                :columns="userList"
                :data="listData"
                @on-selection-change="setSelectList"
            ></Table>
            <!--  -->
            <Page
                class-name="m-page"
                show-elevator
                show-sizer
                show-total
                :total="page.totalSize"
                :page-size="page.size"
                :current="page.page"
                :page-size-opts="page.pageSizeOpts"
                @on-change="changePage"
                @on-page-size-change="changePageSize"
            >
            </Page>
            <div class="clearfix"></div>
        </div>
    </div>
</template>
<script>
    // 
    import Loading from '@/components/Common/Loading'
    // JS
    import Common from 'common/common.js'
    // JS
    import Validate from 'common/validate.js'
    // Api
     import Article from '@/api/Article.js'
    // 
    import tableSetting from 'common/table_setting.js'
    // 
    import TableQuery from 'mixins/table_query.js'
    // 
    import TableOperate from 'mixins/table_operate.js'
    // 
    import Page from 'mixins/page.js'
    export default{
        components:{ Loading },
        mixins: [ TableQuery, TableOperate, Page ],
        computed: {
            //
            apiDisable(){
              return()=>Article.Disable('Information',this.selectList)
            },
            // 
            apiEnable(){
              return () => Article.Enabled('Information',this.selectList);
            },
            // 
            apiDelete(){
              return () => Article.Delete('Information',this.selectList);
            },
        },
        data(){
            return{
                // 
                pageLoading: false,
                //
                queryForm:{
                    //id
                    id:'',
                    //
                    title:'',
                    // 
                    sTime: '',
                    // 
                    eTime: '',
                    //
                    state:'',
                    //
                    webSite:''
                },
                //
                validate:{
//                    webSite:[{pattern: Common.regWebsite , message:'',trigger: 'blur'}],
                    date:[{
                      validator: (rule, value, callback) => {
                        Validate.ValidRangeDate(this.queryForm.sTime, this.queryForm.eTime, callback, false);
                      },
                        trigger: 'change',
                    }]
                },
                //
                userList:[
                    {
                      title:'',
                      type: 'selection',
                      width: 60,
                      align: 'center'
                    },
                    {
                        title:'',
                        key:'id',
                        width:120,
                        align:'center'
                    },
                    {
                        title:'',
                        key:'title',
                        align:'center'
                    },
                    {
                        title:'',
                        key:'keyWord',
                        align:'center'
                    },
                    {
                        title:'',
                        key:'publishDate',
                        width:160,
                        align:'center'
                    },
                    {
                        title:'',
                        key:'webSite',
                        width:200,
                        align:'center'
                    },
                    {
                        title:'',
                        key:'enabled',
                        align:'center',
                        width:80,
                        render:(h,params)=>{
                            return h('span',{
                                attrs:{
                                    class:params.row.enabled == 1?'status-success' : 'status-fail'
                                }
                            },params.row.enabled == 1 ? '' : '')
                        }
                    },
                    {
                        title:'',
                        key: 'operation',
                        align:'center',
                        width:160,
                        render:(h,params)=>{
                            return h('div',[
                                tableSetting.gotoDetail(h, params, this, '', 'ArticleDetail'),
                            ])
                        }
                    }
                ],
                //
                listData:[
//                    {
//                        id:'1',
//                        title:'',
//                        keyWord:'',
//                        publishDate:'',
//                        enabled:'1',
//                        webSite:'www.smt.com'
//                    }
                ]
            }
        },
        created(){
            // 
            this.$store.commit('SET_BREADCRUMB', [
                { name: '', path: '/Home' },
                { name: '', path: '/ArticleManage/List' }
            ]);
            this.getTableList()
        },
        methods:{
            //
            getTableList(query){
                 this.pageLoading = true;
//                console.log(this.page.page)
//                console.log(this.page.size)
//                console.log(this.queryForm)
                Article.List(this.queryForm,this.page.page,this.page.size).then(res=>{
//                    console.log(res)
                  this.pageLoading = false
                  if(res.code==200){
                    this.listData = res.data
                    //
                    this.page = res.paging
                    // 
                    if(query){
                      this.isQuery = true;
                    }
                    else{
                      this.isQuery = false;
                    }
                  }else this.$Message.warning(res.msg)
                }).catch(err=>{
                  console.log(err)
                })
            },
            // 
            setListData(result){

            },
        }
    }
</script>
<style lang="less" scoped>
  @import "../../assets/less/table_list";
  .m-query-form{
      /*overflow: hidden;*/
  }
</style>



