<template>
    <div class="g-content">
        <!--   -->
        <Loading v-if="pageLoading"></Loading>
        <Form v-else ref="infoForm" :model="infoForm" :rules="pageType == 'add'?validate:validateUpdate" :label-width="120">
            <!--  -->
            <h2 class="m-title"></h2>
            <div class="m-content">
                <Row>
                    <Col span="12">
                        <Form-item v-if="pageType == 'edit'" label="">
                            <span>{{ infoForm.createTime }}</span>
                        </Form-item>

                        <Form-item label="" prop="userName">
                            <Input v-model="infoForm.userName" placeholder=""></Input>
                        </Form-item>
                        <!--<Form-item label="" prop="gender">-->
                            <!--<Radio-group v-model="infoForm.gender">-->
                                <!--<Radio label=""></Radio>-->
                                <!--<Radio label=""></Radio>-->
                            <!--</Radio-group>-->
                        <!--</Form-item>-->
                        <Form-item label="" prop="mobile">
                            <Input v-model="infoForm.mobile" placeholder=""></Input>
                        </Form-item>
                    </Col>
                    <Col span="12">
                        <Form-item v-if="pageType == 'edit'" label="">
                            <span>{{ infoForm.id }}</span>
                        </Form-item>
                        <!--<Form-item label="" style="margin-bottom:16px;">-->
                            <!--&lt;!&ndash; -- &ndash;&gt;-->
                            <!--<SingleImage :preview="true" size-hint="100*100px"></SingleImage>-->
                        <!--</Form-item>-->
                        <Form-item label="" prop="email">
                            <AutoComplete v-model="infoForm.email" :data="emailList" @on-search="selectEmail" @on-select="setEmail" placeholder=""></AutoComplete>
                        </Form-item>
                        <Form-item label="" prop="type">
                            <RadioGroup v-model="infoForm.type">
                                <Radio :label="1"></Radio>
                                <Radio :label="1024"></Radio>
                            </RadioGroup>
                        </Form-item>
                    </Col>
                </Row>
            </div>
            <!--  -->
            <h2 class="m-title"><span>()</span></h2>
            <div class="m-content">
                <Row>
                    <Col span="12">
                    <Form-item label="" prop="password">
                        <Input type="password" v-model="infoForm.password" placeholder=""></Input>
                    </Form-item>
                    </Col>
                    <Col span="12">
                    <Form-item label="" prop="passwdCheck">
                        <Input type="password" v-model="infoForm.passwdCheck" placeholder=""></Input>
                    </Form-item>
                    </Col>
                </Row>
            </div>
            <!--  -->
            <div class="m-operation">
                <Button v-if="pageType == 'edit'" class="fr" type="primary" @click="submit('infoForm', 'edit')"></Button>
                <Button v-else class="fr" type="primary" @click="submit('infoForm', 'add')"></Button>
                <Button class="fr" type="ghost" @click="$router.go(-1)"></Button>
                <div class="clearfix"></div>
            </div>
        </Form>
    </div>
</template>

<script>
    // 
    import Loading from 'components/Common/Loading'
    import CompanyName from 'components/Input/FuzzyQuery'
    // 
    import SingleImage from 'components/Image/UploadImage/SingleImage'
    // JS
    import Common from 'common/common.js'
    // 
    import Validate from 'common/validate.js'
    // Api
     import Account from 'api/account.js'
    // 
    import CitySelect from 'mixins/city_select.js'
    // 
    import EmailComplete from 'mixins/email_complete.js'
    // Json
    import JsonCity from 'mock/city.json'
    import JsonData from 'mock/data.json'
    // Vuex
    import { mapGetters } from 'vuex'

    export default {
        components: { Loading, CompanyName, SingleImage },
        mixins: [ CitySelect, EmailComplete ],
        computed: {
            ...mapGetters(['companyName', 'getImageUrl' ])
        },
        data() {
            return {
                // 
                pageLoading: false,
                // 
                pageType: 'add',
                // 
                infoForm: {
                    // 
//                    userId: '',
                    // 
//                    createdAt: '',
                    // 
                    userName: '',
                    // 
//                    face: require('@/assets/images/default-face.jpg'),
                    // 
//                    gender: '',
                    // 
                    mobile: '',
                    // 
                    email: '',
                    // 
                    password: '',
                    // 
                    passwdCheck: '',
                    type:1
                },
                // 
                validate: {
                    userName: [{ required: true, message: '', trigger: 'blur'}],
                    mobile: [
//                        { required: true, message: '', trigger: 'blur'},
                        { pattern: Common.regMobile, message: '', trigger: 'blur' }
                    ],
                    email: [
//                        { required: true, message: '', trigger: 'blur'},
                        { pattern: Common.regEmail, message: '', trigger: 'change' }
                    ],
                    password: [{ required:true ,pattern: Common.regPassword, message: '8-16', trigger: 'blur' }],
                    passwdCheck: [{
                        validator: (rule, value, callback) => {
                            Validate.ValidPwdCheck(this.infoForm.password, value, callback, false);
                        },
                        trigger: 'blur'
                    }]
                },
                //-
                validateUpdate:{
                    mobile: [
                      { pattern: Common.regMobile, message: '', trigger: 'blur' }
                    ],
                    email: [
                      { pattern: Common.regEmail, message: '', trigger: 'change' }
                    ],
                    passwdCheck: [{
                      validator: (rule, value, callback) => {
                        Validate.ValidPwdCheck(this.infoForm.password, value, callback, false);
                      },
                      trigger: 'blur'
                    }],
                }
            }
        },
        created() {
            // 
//            Common.InitPicStore(this);
            // 
            if(this.$route.query.id){
                // 
                this.userId = this.$route.query.id;
                this.$store.commit('SET_BREADCRUMB', [
                    { name: '', path: '/Home' },
                    { name: '', path: '/AccountManage/List' },
                    { name: '', path: '' }
                ]);
                // 
                this.getDetail();
                this.pageType = 'edit';
            }
            else{
                this.pageLoading = false;
                this.$store.commit('SET_BREADCRUMB', [
                    { name: '', path: '/Home' },
                    { name: '', path: '/AccountManage/AccountList' },
                    { name: '', path: '' }
                ]);
            }
        },
        methods: {
            // 
            submit(form, type) {
//                console.log(this.infoForm)
                this.$refs[form].validate((valid) => {
                    if (valid) {
                        // 
                         this.pageLoading = true
//                        this.infoForm.face = this.getImageUrl;
                        if(this.pageType == 'add'){
                            // 
//                            console.log(this.infoForm)
                            this.pageLoading = false
                          if(this.infoForm.email==''&&this.infoForm.mobile==''){
                            this.$Message.error('')
                          }else {
                            Account.Add(this.infoForm).then(res => {
                              if (res.code == 200) {
                                this.$Message.success({
                                  content: '!',
                                  onClose: () => {
                                    // 
                                    this.$router.push({name: 'AccountManage'})
                                  }
                                });
                              } else this.$Message.warning(res.msg)
                            }).catch(err => {
                              console.log(err)
                              this.$Message.error('')
                            })
                          }
                        }
                        else{
                            // 
                          //
                          this.pageLoading = false
                          if(this.infoForm.email==''&&this.infoForm.mobile==''){
                            this.$Message.error('')
                          }else{
                            if(!this.infoForm.password) this.infoForm.password = ''
                            Account.Edit(this.infoForm,this.$route.query.id).then(res=>{
//                                console.log(res)
                              if(res.code==200){
                                this.$Message.success({
                                  content: '!',
                                  onClose: () => {
                                    // 
                                    this.$router.push({ name: 'AccountManage' })
                                  }
                                })
                              }else this.$Message.warning(res.msg)
                            }).catch(err=>{
                              console.log(err)
                              this.$Message.error('')
                            })
                          }
                        }
                    }
                    else this.$Message.error('');
                })
            },
            // 
            getDetail(){
              this.pageLoading = true
//              console.log(this.$route.query.id)
              Account.GetInfo(this.$route.query.id).then(res=>{
//                  console.log(res)
                this.pageLoading = false
                if(res.code==200){
                  this.infoForm = res.data
//                  console.log(this.infoForm.type)
//                  console.log(typeof (this.infoForm.type))
                }else this.$Message.error(res.msg)
              }).catch(err=>{
                this.$Message.error('')
              })
            }
        }
    }
</script>

<style lang="less" scoped>
    .m-operation {
        button, a {
            min-width: 80px;
            margin-left: 10px;
        }
    }
    .ivu-select.select-province {
        float: left;
        width: 32.5%;
        margin-right: 1%;
        &:nth-child(3n) {
            margin-right: 0;
        }
    }
</style>



