<template>
    <div class="g-content">
        <!--   -->
        <Loading v-if="pageLoading"></Loading>
        <Form  v-else ref="editForm" :model="editForm" :rules="validate" :label-width="110" style="width:600px">
            <Form-item label="">
                <span>{{ editForm.ceateTime }}</span>
            </Form-item>
            <Form-item label="">
                <span>{{ editForm.username }}</span>
            </Form-item>
            <Form-item label="" prop="nickname">
                <Input v-model="editForm.nickname" placeholder="" style="width:220px"></Input>
            </Form-item>
            <Form-item label="">
                <Input v-model="editForm.realname" placeholder="" style="width:220px"></Input>
            </Form-item>
            <!--<Form-item label="">
                &lt;!&ndash; -- &ndash;&gt;
                <SingleImage></SingleImage>
            </Form-item>
            <Form-item label="" prop="gender">
                <Radio-group v-model="editForm.gender">
                    <Radio label=""></Radio>
                    <Radio label=""></Radio>
                </Radio-group>
            </Form-item>-->
            <Form-item>
                <Button type="primary" @click="submit('editForm')"></Button>
            </Form-item>
        </Form>
    </div>
</template>

<script>
    // 
    import Loading from 'components/Common/Loading'
	import SingleImage from 'components/Image/UploadImage/SingleImage'
	// Api
     import Account from 'api/account.js'
    // Veux
    import { mapGetters } from 'vuex'
    export default {
        components: {  Loading, SingleImage },
        computed: {
            ...mapGetters([ 'token', 'getImageUrl' ])
        },
		data(){
			return {
                // 
				pageLoading: false,
				// 
                editForm: {
                    // 
                    ceateTime: '',
                    // ()
                    username: '',
                    // 
                    nickname: '',
                    // 
                    realname: '',
                    // 
//                    userface: require('@/assets/images/default-face.jpg'),
//                    // 
//                    gender: '',
                },
				// 
                validate: {
                    nickname: [{ required: true, message: '', trigger: 'blur' }],
//                    gender: [{ required: true, message: '', trigger: 'change' }],
                }
			}
        },
        created(){
            // 
            this.$store.commit('SET_BREADCRUMB', [
                { name: '', path: '/Home' },
                { name: '', path: '/Profile' }
            ]);
            // 
            this.getProfile();
        },
        methods:{
            // 
            submit(form) {
                this.$refs[form].validate((valid) => {
                    if (valid) {
                        // 
                        this.pageLoading = true;
                        this.editForm.userface = this.getImageUrl;
                        // Api.EditProfile(this.editForm, { username : this.editForm.username})
                        // .then(res => {
                        //     this.pageLoading = false;
                        //     if(res.code == 200){
                        //         this.$Message.success('');
                        //         // 
                        //         this.$store.commit('SET_USER_FACE', res.data.attributes.userface);
                        //         this.$store.commit('SET_USER_NICKNAME', res.data.attributes.nickname);
                        //     }
                        //     else this.$Message.error('');
                        // })
                        // .catch(err => console.log(err));
                    }
                    else this.$Message.error('');
                })
            },
            // 
            getProfile(){
                console.log(111111111)
                console.log(this.$store.getters.userId)
                 Account.GetInfo(this.$store.getters.userId)
                 .then(res => {
                     // 
                      console.log(res)
                     if(res.code == 200){
                         this.pageLoading = false;
                         this.editForm = res.data.attributes;
                         // 
//                         this.$store.commit('SET_IMAGE_URL', this.editForm.userface);
                     }
                     else this.$Message.error('!');
                 })
                 .catch(err => console.log(err));
            }
        }
	}
</script>
<style lang="less" scoped>
</style>




