<template>
    <div id="fuzzyQuery" class="f-fuzzy-query">
        <!--  -->
        <Input
            v-model="value"
            placeholder=""
            @input="changeQuery"
            @on-focus="checkQuery"
            @on-blur="hideDropDownList"
            @on-keyup.up="changeUp"
            @on-keydown.down="changeDown"
            @on-enter="selectDropDownItem(optionList[selectIndex].label, selectIndex)"                 
        ></Input>
        <!--  -->
        <div ref="dropDownList" :class="['query-select-dropdown', showList ? '' : 'hidden']">
            <ul v-show="notFound" class="query-select-not-found"></ul>
            <ul v-show="loading" class="query-select-loading"></ul>
            <!--  -->
            <ul v-show="getList" @mouseleave="leaveList" @mouseenter ="enterList">
                <li                        
                    v-for="(item, index) in optionList"
                    :class="['query-select-item', selectIndex === index ? 'query-select-item-selected' : '']"
                    :key="index"                   
                    @click="selectDropDownItem(item.label, index)"
                >{{ item.label }}</li>
            </ul>                    
        </div>
    </div>
</template>
<script>
    // Json
    import JsonData from 'mock/data.json'
    // import { mapState } from 'vuex'
        
    export default {
        name: 'fuzzyQuery',
        computed: {
            // 
            // ...mapState({
            //     value: state => state.common.companyName,
            // }),
            // vuesetter[Vue warn]: Computed property "value" was assigned to but it has no setter.
            // 
            value: {
                // getter  
                get: function() {
                    return this.$store.state.common.companyName
                },  
                // setter  
                set: function(newValue) {
                    this.$store.state.common.value = newValue;
                }
            } 
        },
        data () {
            return {
                // 
                startInput: false,
                // 
                loading: false,
                // 
                // -404
                // -1: 
                state: -404,
                // 
                selectIndex: -1,
                // 
                showList: false,
                // 
                notFound: false,
                // 
                getList: false,
                // 
                getSelect: false,
                // 
                optionList: [],
                // 
                optMaxSize: 10,
            }
        },
        methods: {
            // 
            fuzzyQuery(keyword){
                this.optionList = [];
                this.loading = true;
                this.getList = false;
                this.notFound = false;
                // 
                this.showList = true;
                setTimeout(() => {
                    // json
                    this.setSelectList(JsonData.companyName, keyword);
                }, 200);
            },
            // 
            setSelectList(result, keyword){
                // 
                let list = result.map(item => {
                    return {
                        value: item.id,
                        label: item.name
                    };
                });
                this.optionList = list.filter(item => item.label.indexOf(keyword) > -1);
                // 

                this.loading = false;

                if (this.optionList.length <= 0){
                    this.notFound = true;
                    this.getList = false;
                }
                else{
                    this.notFound = false;
                    this.getList = true;
                }
            },
            // 
            changeQuery(text){
                // 
                if(this.startInput){
                    // 
                    this.$store.commit('SET_COMPANY_NAME', text);
                    this.state = -1;
                    this.getSelect = false;
                    console.log('get search word:' + text);
                    // 
                    if (text == '') {
                        this.optionList = [];
                        // 
                        this.showList = false;
                    }
                    else this.fuzzyQuery(text);
                }
            },
            // 
            selectDropDownItem(text, index){
                // 
                this.value = text;
                // 
                this.selectIndex = index;
                // 
                this.$store.commit('SET_COMPANY_NAME', text);
                // 
                this.showList = false;
                this.getSelect = true;
            },
            // 
            checkQuery(event){
                this.startInput = true;
                let text = event.target._value;
                console.log('get check word:' + text);
                // 
                if (text == '') {                    
                    this.optionList = [];
                    this.notFound = true;
                }
                else this.fuzzyQuery(text);
            },
            // 
            changeUp(){
                if(this.selectIndex <= 0){
                    this.selectIndex = this.optionList.length;
                }
                this.selectIndex--;
                // 
                this.$refs.dropDownList.scrollTop = (this.selectIndex - 5) * 30;
            },
            // 
            changeDown(){
                if(this.optionList.length > 0 && this.selectIndex >= (this.optionList.length - 1)){
                    this.selectIndex = -1;
                }
                this.selectIndex++;
                // 
                if(this.selectIndex >= 5){
                    this.$refs.dropDownList.scrollTop = (this.selectIndex - 5) * 30;
                }
                else this.$refs.dropDownList.scrollTop = 0;
            },
            // 
            enterList(item, index){
                this.getSelect = true;
            },
            // 
            leaveList(item, index){
                this.getSelect = false;
                this.startInput = false;
            },
            // 
            hideDropDownList(){                
                if(this.notFound) this.state = -404;
                // 
                if(this.state == -404 || !this.getSelect){
                    this.showList = false;
                }
            }
        }
    }
</script>
<style lang="less">
    @import "../../assets/less/color";
    .f-fuzzy-query{
        width:100%;
        height:100%;
        position: relative;

        .query-select-dropdown {
            width: inherit;
            max-height: 200px;
            overflow: auto;
            margin: 5px 0;
            padding: 5px 0;
            background-color: #fff;
            box-sizing: border-box;
            border-radius: 4px;
            box-shadow: 0 1px 6px rgba(0,0,0,.2);
            position: absolute;
            z-index: 900;
        }
        .query-select-dropdown.hidden{
            display:none;
        }

        .query-select-loading {
            text-align: center;
            color: #bbbec4;
        }

        .query-select-not-found {
            text-align: center;
            color: #bbbec4;
        }

        .query-select-item {
            margin: 0;
            line-height: normal;
            padding: 7px 16px;
            clear: both;
            color: #495060;
            font-size: 12px!important;
            white-space: nowrap;
            list-style: none;
            cursor: pointer;
            transition: background .2s ease-in-out;
        }

        .query-select-item-focus, .query-select-item:hover {
            background: #f3f3f3;
        }

        .query-select-item-selected, .query-select-item-selected:hover {
            color: #fff;
            background: @base_color;
        }
    }
</style>


