<template>
    <div id="singleImage" class="m-Upload">
        <!--  -->
        <div v-if="getImageUrl != ''" class="upload-show">
            <div class="upload-img-wrap"><img class="upload-image" :src="getImageUrl" @error="notFoundPic"/></div>
            <!--  -->
            <div v-if="preview" class="upload-mask">
                <Icon type="eye" @click.native="viewImage"></Icon>
            </div>
        </div>
        <!--  -->
        <Upload
            :show-upload-list="false"
            :before-upload="beforeUpload"
            :on-success="uploadSuccess"
            :on-error="uploadError"
            :format="format"
            :max-size="fileSize"
            :on-format-error="imageFormatError"
            :on-exceeded-size="imagesMaxSize"
            :action="imageUploadUrl">
            <Button type="ghost" :loading="loading" icon="ios-cloud-upload-outline"></Button>
            <span class="size-hint" v-if="sizeHint"> {{ sizeHint }}</span>
        </Upload>
        <!--   -->
        <template v-if="uploadStatus != 'finished'">
            <Progress v-if="showProgress" :percent="percentage" :stroke-width="3" style="width:340px;"></Progress>
        </template>
        <br/>
        <!--  -->
        <span class="hint">
            * jpgjpegpng
            <span v-if="fileSize < 1024">{{ fileSize }}kb</span>
            <span v-else>{{ Math.floor(fileSize/1024) }}M</span>
            
        </span>
        <div class="clearfix"></div>
        <!--  -->
        <Modal title="" class="m-view-img" v-model="showModal" class-name="vertical-center-modal" width="900">
            <img :src="getImageUrl" @error="notFoundPic"/>
            <div slot="footer"></div>
        </Modal>
    </div>
</template>
<script>
    import Common from '@/common/common.js'
    import { mapGetters } from 'vuex'
    export default {
        name: 'singleImage',
        // previewsizeHintfileSize
        props: {
            // 
            preview:{
                type: Boolean,
                default: false
            },
            sizeHint:{
                type: String,
                default: ''
            },
            fileSize:{
              type: Number,
              default: 150
            }
        },
        computed: {
            ...mapGetters([
                // 
                'getImageUrl',
            ])
        },
        data () {
            return {
                // 
                loading: false,
                // 
                showModal: false,
                // 
                visible: true,
                // Url
                imageUploadUrl: '/api/FileStorage/Image/Upload',
                // 
                format: ['jpg', 'jpeg', 'png'],
                // finished
                uploadStatus: '',
                // 
                showProgress: false,
                // 
                percentage: 0
            }
        },
        methods: {
            // 
            viewImage(){
                this.showModal = true;
            },
            imageFormatError(file) {
                // 
                this.showProgress = false;
                this.$Notice.warning({
                    title: '',
                    desc: ' ' + file.name + '  jpg  png '
                });
                this.loading = false;
            },
            // 
            imagesMaxSize(file) {
                // 
                this.showProgress = false;
                let size = this.fileSize + 'kb';
                if(this.fileSize > 1024) size = Math.floor(this.fileSize/1024) + 'M';
                this.$Notice.warning({
                    title: '',
                    desc: ' ' + file.name + '  ' + size
                });
                this.loading = false;
            },
            // 
            beforeUpload(){
                this.uploadStatus = '';
                this.loading = true;
            },
            // 
            uploadSuccess(res, file) {
                this.loading = false;
                if (res.code == '200') {
                    // 
                    setTimeout(() => {
                        this.uploadStatus = file.status;
                    }, 500);
                    // 
                    this.showProgress = file.showProgress;
                    this.percentage = file.percentage;

                    // ID
                    this.$store.commit('SET_IMAGE_ID', res.data.id);
                    // 
                    this.$store.commit('SET_IMAGE_URL', res.data.url);
                    this.$Notice.success({ title: '!' });

                    this.visible = true;
                }
                else this.$Notice.error({ title: '' });
            },
            // 
            uploadError(res, file) {
                this.loading = false;
                // 
                this.showProgress = false;
                this.$Notice.error({ title: '' });
            },
            // 
            notFoundPic(event){
                Common.SetDefaultPic(event, 2);
            }
        }
    }
</script>
<style lang="less" scoped>
    @import "../../../assets/less/upload_img";
</style>



