<template>
    <div class="g-content">
        <div style="width:500px;">
            <Form ref="editForm" :model="editForm" :rules="validate" :label-width="100">
                <Form-item prop="oldPassword" label="">
                    <Input type="password" v-model="editForm.oldPassword" placeholder=""></Input>
                </Form-item>
                <Form-item prop="newPassword" label="">
                    <Input type="password" v-model="editForm.newPassword" placeholder=""></Input>
                </Form-item>
                <Form-item prop="passwdCheck" label="">
                    <Input type="password" v-model="editForm.passwdCheck" placeholder=""></Input>
                </Form-item>
                <Form-item>
                    <Button type="primary" @click="submit('editForm')"></Button>
                </Form-item>
            </Form>
        </div>
    </div>
</template>

<script>
    // 
	import Validate from 'common/validate.js'
    // Vuex
    import { mapGetters } from 'vuex'
    // Api
    import Account from '@/api/Account.js'
	export default {
        computed: {
            ...mapGetters([ 'token' ])
        },
		data(){
			return {
				// 
				editForm:{
                    // 
                    oldPassword: '',
					// 
					newPassword: '',
					// 
					passwdCheck: '',
				},
				// 
				validate: {
                    oldPassword: [{
                        validator: (rule, value, callback) => {
                            Validate.ValidPassword(value, callback, true);
                        },
                        trigger: 'blur',
                        required: true,
                    }],
                    newPassword:[{
                        validator: (rule, value, callback) => {
                            Validate.ValidPassword(value, callback, true);
                        },
                        trigger: 'blur',
                        required: true,
                    }],
                    passwdCheck:[{
                        validator: (rule, value, callback) => {
                            Validate.ValidPwdCheck(this.editForm.newPassword, value, callback, true);
                        },
                        trigger: 'blur',
                        required: true,
                    }],
                }
			}
        },
        created(){
            // 
            this.$store.commit('SET_BREADCRUMB', [
                { name: '', path: '/Home' },
                { name: '', path: '/ChangePwd' }
            ]);
        },
        methods:{
            // 
            submit(form){
                this.$refs[form].validate((valid) => {
                    if (valid) {
                        //  Api
                      Account.ChangePassword(this.editForm)
                         .then(res => {
                             if(res.code == 200){
                                 //  action
                                 this.$store.dispatch('LogOut')
                                 .then(res => {
                                     this.$Message.success('!');
                                     this.$router.push({ name: 'Login' });
                                 })
                                 .catch(err => console.log(err))
                             }
                             else if(res.code == 404) this.$Message.error('');
                             else this.$Message.error(res.msg);
                         })
                         .catch(err => console.log(err));
                    }
                    else this.$Message.error('');
                })
            },
        }
	}
</script>
<style lang="less" scoped>
</style>



