<template>
    <div class="g-content">
        <!--  -->
        <div class="m-query-form">
            <Form ref="queryForm" :model="queryForm" :rules="queryValid">
                <FormItem class="query-item" prop="id">
                    <Input v-model="queryForm.id" placeholder=""></Input>
                </FormItem>
                <FormItem class="query-item" prop="caseName">
                    <Input v-model="queryForm.caseName" placeholder=""></Input>
                </FormItem>
                <FormItem class="query-item" prop="companyName">
                    <Input v-model="queryForm.companyName" placeholder=""></Input>
                </FormItem>
                <FormItem class="query-item" prop="caseType">
                    <Select v-model="queryForm.caseType" placeholder="">
                        <Option value="" ></Option>
                        <Option v-for="(item, index) in caseTypeList" :value="item.name" :key="index">{{ item.name }}</Option>
                    </Select>
                </FormItem>
                <FormItem class="fl" prop="date">
                    <Date-picker class="query-item" type="date" v-model="queryForm.sTime" placement="bottom-end" placeholder="" @on-change="getStartDate"></Date-picker>
                    <Date-picker class="query-item" type="date" v-model="queryForm.eTime" placement="bottom-end" placeholder="" @on-change="getEndDate"></Date-picker>
                    <div class="clearfix"></div>
                </FormItem>
                <FormItem class="query-item" prop="enabledState">
                    <Select v-model="queryForm.enabledState" placeholder="">
                        <Option value=""></Option>
                        <Option value="1"></Option>
                        <Option value="-1"></Option>
                    </Select>
                </FormItem>
                <FormItem class="fl">
                    <Button class="query-button" type="primary" @click="query('queryForm', 'valid')"></Button>
                    <Button class="query-button" type="ghost" @click="resetQuery('queryForm')"></Button>
                </FormItem>
            </Form>
            <div class="clearfix"></div>
        </div>
        <!--  -->
        <div class="m-operation">
            <Button class="operation-btn" type="primary" @click="addCase"></Button>
            <Button class="operation-btn" :disabled="selectList.length == 0" type="primary" @click="deleteData"></Button>
            <Button class="operation-btn" :disabled="selectList.length == 0" type="primary" @click="enableOrDisable(1)"></Button>
            <Button class="operation-btn" :disabled="selectList.length == 0" type="primary" @click="enableOrDisable(-1)"></Button>
            <div class="clearfix"></div>
        </div>
        <!--   -->
        <Loading v-if="pageLoading"></Loading>
        <div v-else>            
            <!--  -->
            <Table
                :row-class-name="rowClassName"
                class="m-table-list"
                border
                :columns="userList"
                :data="listData"
                @on-selection-change="setSelectList"
            ></Table>
            <!--  -->
            <Page
                class-name="m-page"
                show-elevator
                show-sizer
                show-total
                :total="page.dataCount"
                :page-size="page.pageSize"
                :current="page.pageNo"
                :page-size-opts="page.pageSizeOpts"
                @on-change="changePage"
                @on-page-size-change="changePageSize"
            >
            </Page>
            <div class="clearfix"></div>
        </div>
        <!-- -->
        <Modal v-model="showModal" width="500" @on-cancel="closeModal('paramsForm')">
            <p slot="header">
                <span v-text="paramsForm.caseName == '' ? '' : ''"></span>
            </p>
            <div>
                <Form ref="paramsForm" :model="paramsForm" :rules="paramsValid" :label-width="100">
                    <FormItem label="" prop="caseName">
                        <Input v-model="paramsForm.caseName" placeholder=""></Input>
                    </FormItem>
                    <FormItem label="" prop="companyName">
                        <Input v-model="paramsForm.companyName" placeholder=""></Input>
                    </FormItem>
                    <FormItem label="" prop="caseType">
                        <Select v-model="paramsForm.caseType" placeholder="">
                            <Option value="" ></Option>
                            <Option v-for="(item, index) in caseTypeList" :value="item.name" :key="index">{{ item.name }}</Option>
                        </Select>
                    </FormItem>
                </Form>
            </div>
            <div slot="footer">
                <Button size="large" @click="closeModal('paramsForm')"></Button>
                <Button type="primary" size="large" @click="operation('paramsForm', operateType)"></Button>
            </div>
        </Modal>
    </div>
</template>

<script>
    // 
    import Loading from '@/components/Common/Loading'
    // JS
    import Common from 'common/common.js'
    // JS
    import Validate from 'common/validate.js'
    // Api
    // import Api from '@/api/api.js'
    // Json
    import JsonData from 'mock/data.json'
    // 
    import tableSetting from 'common/table_setting.js'
    // 
    import TableQuery from 'mixins/table_query.js'
    // 
    import TableOperate from 'mixins/table_operate.js'
    // 
    import Page from 'mixins/page.js'

    export default {
        components: { Loading },
        mixins: [  TableQuery, TableOperate, Page ],
        data() {
            return {
                // 
                pageLoading: false,
                // 
                caseTypeList: [],
                // 
                queryForm: {
                    // 
                    id: '',
                    // 
                    caseName: '',
                    // 
                    companyName: '',
                    // 
                    caseType: '',
                    // 
                    sTime: '',
                    // 
                    eTime: '',
                    // 
                    enabledState: '',
                },
                // 
                queryValid: {},
                // 
                paramsForm: {
                    id: '',
                    caseName: '',                    
                    companyName: '',
                    caseType: ''
                },
                // 
                paramsValid: {
                    caseName:[{ required: true, message: '', trigger: 'blur' }],
                    companyName:[{ required: true, message: '', trigger: 'blur' }],
                    caseType: [{ required: true, message: '', trigger: 'change' }],
                },
                // 
                showModal: false,
                // :1, 2
                operateType: 0,
                // 
                userList:[
                    {
                        title: '',
                        type: 'selection',
                        key: 'selectAll',
                        width: '60',
                        align: 'center',
                    },
                    {
                        title: '',
                        key: 'id',
                        align: 'center',
                    },
                    {
                        title: '',
                        key: 'caseName',
                        align: 'center'
                    },
                    {
                        title: '',
                        key: 'companyName',
                        align: 'center'
                    },
                    {
                        title: '',
                        key: 'caseType',
                        align: 'center'
                    },
                    {
                        title: '',
                        key: 'createTime',
                        align: 'center',
                    },
                    {
                        title: '',
                        key: 'enabledState',
                        align: 'center',
                        render: (h, params) => {
                            return h('span', {
								attrs: {
									class: params.row.enabledState == 1 ? 'status-success' : 'status-fail',
								}
							}, params.row.enabledState == 1 ? '' : '');
                        }
                    },
                    {
                        title: '',
                        width: '120',
                        key: 'operation',
                        align: 'center',
                        render: (h, params) => {
                            return h('div', [
                                tableSetting.popUp(h, params, this, '', 0),
                            ]);                          
                        }
                    }
                ],
                //
                listData:[
                    {
                        id: 1,
                        caseName: '1',
                        companyName: '',
                        caseType: '',
                        createTime: '2018-2-28',
                        enabledState: 1
                    },
                    {
                        id: 2,
                        caseName: '2',
                        companyName: '',
                        caseType: '',
                        createTime: '2018-2-28',
                        enabledState: 1
                    },
                    {
                        id: 3,
                        caseName: '3',
                        companyName: '',
                        caseType: '',
                        createTime: '2018-2-28',
                        enabledState: 1
                    }
                ],
            }
        },
        created(){
            // 
            this.$store.commit('SET_BREADCRUMB', [
                { name: '', path: '/Home' },
                { name: '', path: '/CaseManage/List' }
            ]);
            // 
            this.getTableList();
            // 
            this.caseTypeList = JsonData.caseType;
        },
        methods: {
            // 
            getTableList(query){
                // this.pageLoading = true;
                // 
                if(query){
                    this.isQuery = true;
                }
                else{
                    this.isQuery = false;
                }
            },
            // 
            setListData(result){
            },
            // 
            openModel(params){
                this.showModal = true;
                this.paramsForm.caseName = params.caseName;
                this.paramsForm.companyName = params.companyName;
                this.paramsForm.caseType = params.caseType;
                this.paramsForm.id = params.id;
            },
            // 
            closeModal(name){
                this.showModal = false;
                // 
                this.$refs[name].resetFields();
            },
            // 
            operation(name, type){
                // 
                this.$refs[name].validate((valid)=>{
                    if(valid){
                        this.paramsForm.type = parseInt(this.paramsForm.type);
                        // 
                        if(type == 1) this.addData();
                        else if(type == 2) this.EditData();
                        // 
                        setTimeout(() => {
                            this.closeModal(name);
                        }, 500);
                    }
                    else this.$Message.error('');
                })
            },
            addCase(){
                this.showModal = true;
                this.operateType = 1;
            },
        }
    }
</script>

<style lang="less" scoped>
    @import "../../assets/less/table_list";
</style>


